/* @import 'tailwindcss/base';
@import 'tailwindcss/components'; */

/* set init css */
*,html {
  box-sizing: border-box;
}
body {
  font-family: 'NanumSquare', sans-serif;
  margin: 0;
  padding: 0;
  color: #3e3e3e;
}
h1,h2,h3,h4,h5,h6,p,div,article,ul,ol,li,header,aside,footer,fieldset {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
ul,ol,li {
  list-style-type: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: initial;
}

/* common css */
.container {
  margin: 0 auto;
}
.content {
  padding: 0 15px;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}
.content h2 {
  text-align: center;
  font-size: 26px;
  margin: 10px 0 35px;
}
.content h3 {
  text-align: center;
  font-size: 20px;
  margin: 10px 0;
}
.display-flex {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

/* header */
header .wrap {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px 15px;
  justify-content: space-between;
}
header h1 a {
  color: #3e3e3e;
  font-weight: 900;
}
nav a {
  margin: 0 0 0 25px;
  padding: 0 15px;
  display: block;
  color: #3e3e3e;
  font-size: 16px;
  font-weight: 500;
  line-height: 35px;
  text-transform: uppercase;
  font-weight: 500;
}

/* footer */
footer {
  background-color: #3e3e3e;
}
footer .wrap {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px 15px;
  text-align: center;
  color: grey;
  font-size: 45px;
}

/* main page */
#mainPage img {
  max-width: 612px;
  width: 100%;
  margin: 0 auto
}
#listTable td.tal {
  text-align: left;
}
form {
  width: 100%;
  margin: 0 auto;
}
label {
  font-size: 20px;
  display: block;
  margin: 20px 0 10px;
}
input {
  width: 100%;
  padding: 10px 10px;
  display: block;
  border: 1px solid #ccc;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  font-size: 16px !important;
  color: #000 !important;
}
textarea {
  resize: none;
  width: 100%;
  height: 250px;
  padding: 20px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  font-size: 16px;
}
.btn {
  width: 100%;
}

/* board */
.board-card {
  margin: 0 auto 80px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.btn-wrap {
  margin : 0 auto 100px;
  display : flex;
  flex-wrap : wrap;
  flex-direction : row;
  justify-content : space-between;
}
.btn-wrap a {
  width : 48%;
}
.btn-normal {
  padding : 8px 0;
  display : block;
  text-align : center;
  border : 1px solid #3e3e3e;
}
.btn-delete {
  padding : 8px 0;
  display : block;
  text-align : center;
  color : #fff;
  background : coral;
}